.blog__poster {
  width: 100%;
  height: 250px;
  overflow: hidden;
}

.blog__poster img {
  width: 100%;
}

.blog__content {
  width: 1100px;
  margin: 20px auto 0;
}

.blog__top {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.blog__exp {
  font-size: 14px;
}

.blog__line {
  display: block;
  width: 100%;
  height: 0;
  border-bottom: 1px solid #bfbfbf;
}


